<template>
	<view>
		
	</view>
</template>

<script>
	import Vue from 'vue'
	import service from '../../service.js';
	import uniSwipeAction from '../../components/uni-swipe-action/uni-swipe-action.vue'
	import uniSwipeActionItem from '../../components/uni-swipe-action-item/uni-swipe-action-item.vue'
	
	import {
		mapState,
		mapMutations
	} from 'vuex'
	var that 
	export default {
		data() {
			return {
				pltype: 1,
				title: '列表',
				btnkg: 0,
				modalName: null,
				alltype:false,
				page:1,
				pagesize:20,
				datas: [],
				options:[
					{
							text: '编辑',
							style: {
									color:'#3171F5',
									backgroundColor: 'rgba(87,107,149,.2)'
							}
					}, {
							text: '删除',
							style: {
									color: '#E64340',
									backgroundColor: 'rgba(230,67,64,.2)'
							}
					}
				]
			}
		},
		components: {
		    uniSwipeAction,
		    uniSwipeActionItem
		},
		computed: {
			...mapState(['hasLogin', 'forcedLogin', 'userName','loginDatas'])
		},
		onLoad() {
			that=this
			that.datas=[
				{
					pic:'/static/images/goods_type_03.jpg',
					name:'笔类',
					id:1
				},
				{
					pic:'/static/images/goods_type_05.jpg',
					name:'手表',
					id:2
				},
				{
					pic:'/static/images/goods_type_07.jpg',
					name:'行李箱',
					id:3
				}, 
				{
					pic:'/static/images/goods_type_12.jpg',
					name:'男装',
					id:4
				},
				{
					pic:'/static/images/goods_type_13.jpg',
					name:'灯具',
					id:5
				},
				{
					pic:'/static/images/goods_type_14.jpg',
					name:'鞋子',
					id:6
				},
			]
			// this.getdata()
		},
		/**
		 * 页面相关事件处理函数--监听用户下拉动作
		 */
		onPullDownRefresh: function () {
			this.page=1
		  this.getdata()
		},
		
		/**
		 * 页面上拉触底事件的处理函数
		 */
		onReachBottom: function () {
			this.getdata()
		},
		methods: {
			onClick(e,idx,id,item){
				console.log(e)
				console.log(idx)
				// console.log('当前点击的是第'+e.index+'个按钮，点击内容是'+e.content.text)
				if(e.index==0){
					console.log(item)
					uni.navigateTo({
						url:'../fabu_set/fabu?id='+id+'&type='+item.type
					})
				}
				if(e.index==1){
					this.sc_d_fuc(id)
				}
			},
			getdata(){
				// /api/my/myCollect
				var that =this
				var data = {
					token:that.loginDatas.token,
					page:that.page,
					per_page:that.pagesize
				}
				var jkurl = '/api/my/issue'
				console.log(that.btnkg)
				if(that.btnkg==1){
					return
				}else{
					that.btnkg=1
				}
				service.post(jkurl, data,
					function(res) {
						
						// if (res.data.code == 1) {
						if (res.data.code == 1) {
							
							var datas = res.data.data.data
							console.log(typeof datas)
							
							if (typeof datas == 'string') {
								datas = JSON.parse(datas)
							}
							if(that.page==1){
								that.datas=datas
								that.page++
								that.btnkg=0
							}else{
								that.btnkg=0
								if(datas.length==0){
									uni.showToast({
										icon:'none',
										title:'暂无更多数据'
									})
									return
								}
								that.datas=that.datas.concat(datas)
								that.page++
							}
							
								
								
				
						} else {
							that.btnkg=0
							if (res.data.msg) {
								uni.showToast({
									icon: 'none',
									title: res.data.msg
								})
							} else {
								uni.showToast({
									icon: 'none',
									title: '操作失败'
								})
							}
						}
					},
					function(err) {
						that.btnkg=0
						
							uni.showToast({
								icon: 'none',
								title: '获取数据失败'
							})
					
					}
				)
			},
			
			back_fuc() {
				uni.navigateBack()
			},
			call_tel(e) {
				uni.makePhoneCall({
					phoneNumber: e.currentTarget.dataset.tel + ''
				});
			},
			togglePay(item, index) {
				if (item.active) {
					Vue.set(item, 'active', false); //为item添加不存在的属性，需要使用vue提供的Vue.set( object, key, value )方法。 
				} else {
					Vue.set(item, 'active', true);
				}
			},
			sc_all(){
				var that=this
				var datas=that.datas
				var arr=[]
				for(var i=0;i<datas.length;i++){
					if(that.alltype==true){
						Vue.set(datas[i], 'active', false);
						
					}else{
						Vue.set(datas[i], 'active', true);
						
					}
					
				}
				that.alltype=!that.alltype
			},
			sc_fuc(){
				var that =this
				console.log(this.datas)
				var datas=this.datas
				var arr=[]
				for(var i=0;i<datas.length;i++){
					if(datas[i].active){
						arr.push(datas[i].id)
					}
				}
				Dialog.confirm({
					message: '确定要删除这些房源吗？',
				}).then(() => {
					console.log('close');
					var data = {
						ids:arr.join(','),
						token:that.loginDatas.token,
					}
					console.log(data)
					// return
					//selectSaraylDetailByUserCard
					var jkurl = '/api/my/issueDelete'
					
					
					service.post(jkurl, data,
						function(res) {
							
							// if (res.data.code == 1) {
							if (res.data.code == 1) {
								
								
								
									
									uni.showToast({
										icon:'none',
										title:'操作成功'
									})
									that.page=1
									that.getdata()
									that.btnkg=0
					
							} else {
								that.btnkg=0
								if (res.data.msg) {
									uni.showToast({
										icon: 'none',
										title: res.data.msg
									})
								} else {
									uni.showToast({
										icon: 'none',
										title: '操作失败'
									})
								}
							}
						},
						function(err) {
							that.btnkg=0
							
								uni.showToast({
									icon: 'none',
									title: '获取数据失败'
								})
						
						}
					)
					
				});
				console.log(arr)
			},
			// sc_d_fuc(e){
			sc_d_fuc(id){
				var that =this
				Dialog.confirm({
					message: '确定删除该房源吗？',
				}).then(() => {
					// console.log(e.currentTarget.dataset.id)
					// console.log(id)
					console.log('close');
					// var datas=e.currentTarget.dataset
					var data = {
						ids:id,
						token:that.loginDatas.token,
					}
					console.log(data)
					// return
					//selectSaraylDetailByUserCard
					var jkurl = '/api/my/issueDelete'
					
					
					service.post(jkurl, data,
						function(res) {
							
							// if (res.data.code == 1) {
							if (res.data.code == 1) {
								
								
								
									
									uni.showToast({
										icon:'none',
										title:'操作成功'
									})
									that.page=1
									that.getdata()
									that.btnkg=0
					
							} else {
								that.btnkg=0
								if (res.data.msg) {
									uni.showToast({
										icon: 'none',
										title: res.data.msg
									})
								} else {
									uni.showToast({
										icon: 'none',
										title: '操作失败'
									})
								}
							}
						},
						function(err) {
							that.btnkg=0
							
								uni.showToast({
									icon: 'none',
									title: '获取数据失败'
								})
						
						}
					)
				});
				// console.log(arr)
			},
			onClose({
				detail
			}) {
				const {
					position,
					instance
				} = detail;
				switch (position) {
					case 'left':
						instance.close();
						break;
					case 'cell':
					case 'outside':
					case 'right':
						console.log('right')
						instance.close();
						// Dialog.confirm({
						// 	message: '确定删除该房源吗？',
						// }).then(() => {
						// 	console.log('close');
						// 	instance.close();
						// });
						break;
				}
			},
			jump(e) {
				var that = this
			
				if (that.btnkg == 1) {
					return
				} else {
					that.btnkg = 1
					setTimeout(function() {
						that.btnkg = 0
					}, 1000)
				}
				var datas = e.currentTarget.dataset
			
				console.log(datas.url)
				uni.navigateTo({
					url: e.currentTarget.dataset.url,
				});
			},
			pl_fuc() {
				console.log(1)
				this.pltype = !this.pltype
				var datas=this.datas
				var arr=[]
				for(var i=0;i<datas.length;i++){
					Vue.set(datas[i], 'active', false);
				}
			}
		}
	}
</script>

